<template>
  <header>
    <h1>{{title}}</h1>
    <Button v-show="homePage" @btn-click="$emit('toggle-show-add-department')" :text="showAddDepartment ? 'Close':'Add Department'" :color="showAddDepartment ? 'red' : 'green'"/>
<!--    <Button text="Update Department" color="blue"/>-->
<!--    <Button text="Delete Department" color="red"/>-->
  </header>
</template>

<script>
import Button from "@/components/Button";

  export default {
    name: 'Header',
    props: {
      title: String,
      showAddDepartment: Boolean
    },
    components: {
      Button,
    },
    computed: {
      homePage(){
        if (this.$route.path === '/'){
          return true
        } else {
          return false
        }
      }
    }
  }
</script>

