<template>
  <AddDepartment v-show="showAddDepartment" @add-department="addDepartment"/>
  <Departments @toggle-employeeList="toggleEmployeeList" @delete-department="deleteDepartment" :departments="departments"/>
</template>

<script>
import Departments from "@/components/Departments";
import Employees from "@/components/Employees";
import AddDepartment from "@/components/AddDepartment";

  export default {
    name: 'Home',
    props: {
      showAddDepartment: Boolean
    },
    components: {
      Departments,
      Employees,
      AddDepartment
    },
    data() {
      return{
        departments: []
      }
    },
    methods: {
      async addDepartment(department){
        const res = await fetch('api/departments',{
          method: 'POST',
          headers: {
            'Content-type': 'application/json',
          },
          body: JSON.stringify(department),
        })
        const data = await res.json()
        this.departments = [...this.departments,data]
      },
      async deleteDepartment(id) {
        if (confirm('Are you sure?')) {
          const res = await fetch(`api/departments/${id}`,{
            method: 'DELETE',
          })

          res.status === 200 ? (this.departments = this.departments.filter((department) => department.id !== id)) : alert('Error deleting depertment')
        }
      },
      async toggleEmployeeList(id){
        const departmentToToggle = await this.fetchDepartment(id)
        const updDepartment = {...departmentToToggle,employeeList: !departmentToToggle.employeeList}
        const res = await fetch(`api/departments/${id}`,{
              method: 'PUT',
              headers: {
                'Content-type':'application/json'
              },
              body: JSON.stringify(updDepartment)
            }
        )

        const data = await res.json()

        this.departments = this.departments.map((department) => department.id === id ? {...department,employeeList: data.employeeList}: department)
      }

      ,
      async fetchDepartments(){
        const res = await fetch('api/departments')
        const data = await res.json()
        return data
      },
      async fetchDepartment(id){
        const res = await fetch(`api/departments/${id}`)
        const data = await res.json()

        return data
      },


    }
    /*  ,
      mounted() {
        axios
          .get('http://localhost:8080/api/departments')
          .then(response => (this.departments = response))
      }*/
    ,
    async created() {
      this.departments = await this.fetchDepartments()
    }

  }
</script>